<template>
  <div class="list">
    <table cellpadding="10" cellspacing="0">
      <tr>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
      </tr>
      <tr
        v-for="(item, index) in list"
        :key="item.id"
        @click="showminiPlayer(index)"
      >
        <td>{{ index + 1 }}</td>
        <td>
          <i class="iconfont icon-xihuan"></i>
        </td>
        <td>{{ item.title }}</td>
        <td>{{ item.singer }}</td>
        <td>{{ item.album }}</td>
        <td>{{ item.dt | timeformat }}</td>
      </tr>
    </table>
  </div>
</template>

<script>
  import { mapState } from 'vuex';
  import moment from 'moment';
  export default {
    name: 'list',
    computed: {
      ...mapState({
        list: (state) => state.desc.songList,
        show: (state) => state.showPlayer.show,
      }),
    },
    methods: {
      showminiPlayer(index) {
        //Vuex
        this.$store.commit({
          type: 'play/selectSongByIndex',
          index: index,
          list: this.list,
        });
        //truefalse
        if (this.show) {
          this.$store.commit({
            type: 'showPlayer/changeState',
          });
        } else {
          return;
        }
      },
    },
    filters: {
      timeformat(val) {
        return moment(val).format('mm:ss');
      },
    },
  };
</script>

<style lang="scss" scoped>
  .list {
    width: 100%;
    table {
      width: 100%;
      font-size: 14px;
      tr {
        line-height: 32px;
        font-size: 12px;
        border: none;
        color: #7f8c8d;
        cursor: pointer;
        &:hover {
          color: rgba(100, 181, 246, 1);
        }
      }
      td:nth-child(1) {
        width: 3%;
        text-align: left;
      }
      td:nth-child(2) {
        width: 8%;
        text-align: center;
      }
      td:nth-child(3) {
        text-align: left;
        width: 20%;
      }
      td:nth-child(4) {
        text-align: left;
        width: 30%;
      }
      td:nth-child(5) {
        text-align: left;
        width: 40%;
      }
    }
  }
</style>
