<template>
  <div class="RankingList">
    <TopRank :topRankSource="TopSource"></TopRank>
    <Allrank :allRankSource="allrankSource"></Allrank>
  </div>
</template>

<script>
  import Allrank from '@/views/rankingList/Allrank.vue';
  import TopRank from '@/views/rankingList/TopRank.vue';
  export default {
    data() {
      return {
        TopSource: [],
        allrankSource: [],
        idList: [],
        musicList:[]
      };
    },
    components: {
      TopRank,
      Allrank,
    },
    methods: {
      async getrankList() {
        //
        let loading = this.$loading({
          lock: true,
          text: 'loading...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)',
        });
        //
        await this.api
          .getrankList()
          .then((data) => {
            const newdata = data.data.list.map((item) => ({
              id: item.id,
              name: item.name,
              des: item.description,
              update: item.updateFrequency,
              img: item.coverImgUrl,
              playCount: item.playCount,
              updateTime: item.updateTime,
            }));
            //
            // newdata.pop();
            //6
            this.TopSource = [...newdata.splice(0, 6)];
            this.allrankSource = newdata;
          })
          .catch((error) => {
            this.$message.error('');
          });
        //
        loading.close();
      },
    },
    created() {
      this.getrankList();
    },
  };
</script>

<style lang="scss" scoped>
  .RankingList {
    width: 100%;
  }
</style>
