<template>
  <div class="playlists-container">
    <!--  -->
    <div
      class="playlist-high-quality"
      :style="`background-image: url('${topCard.coverImgUrl}')`"
    >
      <div class="playlist-high-quality-mask">
        <div class="show-Pic">
          <img :src="topCard.coverImgUrl" alt="" />
        </div>
        <div class="show-text">
          <h2></h2>
          <h3>{{ topCard.name }}</h3>
          <div class="desc">{{ topCard.description }}</div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="category-tags" ref="tags">
      <div class="tag" v-for="item in Category" :key="item.id" @click="filterplaylist(item.name)">{{ item.name }}</div>
    </div>
    <!--  -->
    <div class="playlist-card-body">
      <playlistCard
        :playlist="renderlist"
      ></playlistCard>
      <div class="page">
        <el-pagination
          @current-change="handleCurrentChange"
          layout="prev, pager, next"
          :total="pagelength"
          :page-size="pageSize"
          :current-page="currentPage"
          prev-text=""
          next-text=""
        ></el-pagination>
      </div>
    </div>
    <!--  -->
  </div>
</template>

<script>
  import playlistCard from '@/views/recommend/chilren/PlaylistCard.vue';
  import { mapGetters, mapState } from 'vuex';
  export default {
    name: 'playlists',
    data() {
      return {
        topCard: {
          coverImgUrl: '',
          description: '',
          name: '',
        },
        Category: [],
      };
    },
    computed: {
      ...mapState({
        currentPage: (state) => state.paging.currentPage,
        pageSize: (state) => state.paging.pageSize,
      }),
      //vuex  renderlist filter
      ...mapGetters({
        renderlist: 'paging/renderPlaylistArr',
        pagelength:'paging/pageLength',
      }),
    },
    components: {
      playlistCard,
    },
    methods: {
      //
      controlBg(e){
        console.log(e.target);
      },
      //
      filterplaylist(filterTag) {
        this.$store.commit({
          type:'paging/selectFilterTag',
          keyword:filterTag
        })
      },
      //
      handleCurrentChange(val) {
        this.$store.commit({
          type: 'paging/selectCurrentPage',
          pageNum: val,
        });
      },
      async getData() {
        //
        const loading = this.$loading({
          lock: true,
          text: 'loading...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)',
        });
        // 

        await this.api
          .get('/top/playlist/highquality?limit=10')
          .then((data) => {
            const { coverImgUrl, description, name } = data.data.playlists[0];
            this.topCard.coverImgUrl = coverImgUrl;
            this.topCard.description = description;
            this.topCard.name = name;
          });

        //
        await this.api.get('/top/playlist?limit=100').then((data) => {
          const dataSource = data.data.playlists.map((item) => ({
            id: item.id,
            text: item.name,
            img: item.coverImgUrl,
            playTime: item.playCount,
            tags: item.tags,
          }));
          this.$store.commit({
            type: 'paging/selectPlaylist',
            allPlaylist: dataSource,
          });
          this.dataToplaylistCard = dataSource;
        });
        await this.api.get('/playlist/hot').then((data) => {
          const tags = data.data.tags.map((item) => ({
            id: item.id,
            name: item.name,
          }));
          this.Category = tags;
        });
        // 
        loading.close();
      },
    },
    created() {
      this.getData();
    },
    mounted(){
      let tag = document.querySelector('.category-tags');
      let tags = tag.childNodes;
      tag.addEventListener('click',(e)=>{
        //
        for(let i = 0; i < tags.length ; i++){
          tags[i].style.background = '';
          tags[i].style.color = '';
        }
        //
        e.target.style.background = 'rgba(189,195,199,.5)';
        e.target.style.color = 'rgba(100, 181, 246, 1)';
        //
        tag.style.background = '';
      });
    }
  };
</script>

<style lang="scss" scoped>
  .playlists-container {
    width: 100%;
    .playlist-high-quality {
      position: relative;
      width: 100%;
      height: 200px;
      border-radius: 8px;
      background: rgba(0, 0, 0, 0.2);
      overflow: hidden;
      .playlist-high-quality-mask {
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.5);
        backdrop-filter: blur(15px);
        .show-Pic {
          position: absolute;
          left: 2.5%;
          top: 50%;
          transform: translateY(-50%);
          width: 160px;
          height: 160px;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .show-text {
          position: absolute;
          right: 2%;
          top: 50%;
          transform: translateY(-50%);
          width: 81%;
          height: 160px;
          color: #fff;
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          align-items: flex-start;
          cursor: default;
          h2 {
            width: 100px;
            font-size: 18px;
            font-weight: normal;
            color: #f39c12;
            border: 1px solid #f39c12;
            border-radius: 3px;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
          }
          h3 {
            font-weight: normal;
          }
          .desc {
            width: 100%;
            font-size: 14px;
            // white-space: nowrap;
            // text-overflow: ellipsis;
            // overflow: hidden;
          }
        }
      }
    }
    .category-tags {
      margin-top: 15px;
      width: 100%;
      height: 16px;
      font-size: 12px;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      .tag {
        margin-right: 10px;
        width: 60px;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 2px;
        cursor: pointer;
        &:hover {
          background: #bdc3c7;
        }
        &:last-child {
          margin-right: 0;
        }
      }
    }
    .playlist-card-body {
      margin-top: 5px;
      width: 100%;
      height: 30px;
      .page {
        margin-top: 30px;
        margin-bottom: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
</style>
