<template>
  <div class="recommend">
    <Banner :Banner="bannerSource"></Banner>
    <Tjmusic :musicSource="TjmusicSource"></Tjmusic>
    <Hotplayer :HotplayerSource="HotplayerSource"></Hotplayer>
    <Newsong :newsongSource="newSongSource"></Newsong>
    <RecomMV :recomMVSource="RecomMVSource"></RecomMV>
  </div>
</template>

<script>
  import Banner from '@/views/recommend/Banner.vue';
  import Tjmusic from '@/views/recommend/Tjmusci.vue';
  import Hotplayer from '@/views/recommend/Hotplayer.vue';
  import Newsong from '@/views/recommend/Newsong.vue';
  import RecomMV from '@/views/recommend/RecomMV.vue';
  export default {
    data() {
      return {
        bannerSource: [],
        newSongSource: [],
        TjmusicSource: [],
        RecomMVSource: [],
        HotplayerSource: [],
        params:''
      };
    },
    methods: {
      async getData() {
        //
        const loading = this.$loading({
          lock: true,
          text: 'loading...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)',
        });

        //
        //banner
        await this.api
          .getBanner()
          .then((data) => {
            const dataSource = data.data.banners.map((item) => ({
              encodeId: item.encodeId,
              img: item.imageUrl,
              title: item.typeTitle,
            }));
            // id encodeId encodeIdkey
            for (var i = 0; i < dataSource.length; i++) {
              dataSource[i].id = i;
            }
            this.bannerSource = dataSource;
          })
          .catch((error) => {
            console.log('banner', error);
          });

        //
        await this.api
          .getPlayList()
          .then((data) => {
            const dataSource = data.data.result.map((item) => ({
              id: item.id,
              text: item.name,
              img: item.picUrl,
              playTime: item.playCount,
            }));
            this.TjmusicSource = dataSource;
          })
          .catch((error) => {
            console.log('', error);
          });

        //
        await this.api
          .getSingleHome()
          .then((data) => {
            const newData = data.data.result.map((item) => ({
              id: item.id,
              picUrl: item.picUrl,
              name: item.name,
            }));
            this.HotplayerSource = newData;
          })
          .catch((error) => {
            console.log('', error);
          });

        //id
        await this.api
          .getNewSong()
          .then((data) => {
            const dataSource = data.data.result.map((item) => ({
              id: item.id,
            }));
            //
            let result = '';
            dataSource.map((item) => {
              result += item.id + ',';
              return result;
            });
            result = result.slice(0, result.length - 1);
            this.params = result;
          })
          .catch((error) => {
            console.log('', error);
          });
          //
          await this.api.get('/song/detail?ids=' + this.params).then((data) => {
            const dataSource = data.data.songs.map(item => ({
              id:item.id,
              alia: item.alia[0],
              singer:item.ar[0].name,
              title: item.name,
              picUrl:item.al.picUrl,
              mvid:item.mv,
              dt:item.dt,
              transName:item.tns
            }));
            //miniplayer
            this.newSongSource = dataSource;
        });

        //MV
        await this.api
          .getMV()
          .then((data) => {
            const newData = data.data.result.map((item) => ({
              id: item.id,
              name: item.artistName,
              desc: item.name,
              img: item.picUrl,
            }));
            this.RecomMVSource = newData;
          })
          .catch((error) => {
            console.log('MV', error);
          });
        //
        loading.close();
      },
    },
    created() {
      this.getData();
    },
    components: {
      Banner,
      Tjmusic,
      Hotplayer,
      Newsong,
      RecomMV,
    },
  };
</script>

